/* Navbar */
.main {
    font-family: 'IBM Plex Sans', sans-serif;
    opacity: 0.9;
}

.nav-item {
    margin-right: .5rem;
}

.logos {
    display: flex;
    justify-content: center;
}

.nav-logo {
    text-decoration: none;
    cursor: pointer;
    font-family: 'Marmelad', sans-serif;
    font-size: 1.85rem;
    color: #d5f5f8;
}

.nav-logo:hover {
    color: #6C63FF;
    text-decoration: none;
    transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
}

.logo:hover {
    color: #6C63FF;
}

.navbar-dark .navbar-nav .active>.nav-link {
    font-size: 1.1rem;
    transition: all 0.3s;
}

.navbar-dark .navbar-nav .active>.nav-link:hover {
    color: #6C63FF;
}

.nav-log-img:hover {
    transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
    position: relative;
    bottom: 1px;
}

/* Landing */
#landing .container {
    height: 100vh;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-template-areas:
        "heading    img"
        "signup     img";
    grid-column-gap: 10px;
    grid-row-gap: 10px;
}

.heading {
    grid-area: heading;
    height: 35vh;
    align-self: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-radius: .50rem;
}

.signup {
    grid-area: signup;
    height: 300px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(1, 1fr);
    grid-template-areas:
        "doctor user";
    grid-column-gap: 10px;
    grid-row-gap: 10px;
}

.doctor-signup {
    grid-area: doctor;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.user-signup {
    grid-area: user;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.img {
    /* background-color: lightgreen; */
    grid-area: img;
    height: auto;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(1, 1fr);
    grid-template-areas:
        "pic1";
    grid-column-gap: 10px;
    grid-row-gap: 10px;
}

.img-1 {
    grid-area: pic1;
    align-self: center;
    width: 100%;
}

.img-1 img {
    width: 100%;
    height: auto;
}

/* Login Page */
/* Login Page and register page */

.common-form {
    height: 100vh;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-template-areas:
        "form-side    img-side"
        "form-side     img-side";
    grid-column-gap: 10px;
    grid-row-gap: 10px;
}

.form-side {
    grid-area: form-side;
    align-self: center;
}

.form-side .heading-common {
    align-self: baseline;
}

.img-side {
    grid-area: img-side;
    align-self: center;
}

.register-user {
    width: 80vh;
}

/* End of login and register page */

.add-edit-btn {
    display: flex;
    margin: 1.9rem auto;
}

.add-edit-btn>a {
    margin-right: 1rem;
}

.common-table {
    width: 50rem;
    border-radius: 0.25rem;

}

.scroll-table {
    height: 15rem;

    overflow: auto;


    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    background: linear-gradient(45deg, #faf9fa, #efe7ef);
   
}

.table {
    border-radius: 0.5rem;
}

.table thead tr:nth-child(1) th {
    position: sticky;
    top: 0;
}

.table th,
.table td {
    font-family: 'IBM Plex Sans', sans-serif;
    padding: 1rem 0;
    text-align: center;
    background-color: #f4f4f4;
}

.table th {
    font-size: 1.25rem;
    background: #535050;
    vertical-align: baseline;
    color: #f4f4f4;
}

.review-graph {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(1, 1fr);
    grid-template-areas:
        "common-details common-details";
    grid-column-gap: 1rem;
}

.common-details {
    display: flex;
    flex-direction: column;
    height: auto;
    justify-content: space-between;
}

#profiles-page .profiles {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: normal;
}

.profile-1 {
    grid-area: profile-1;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(1, 1fr);
    grid-template-areas:
        "profile-img profile-details profile-buttons"
    ;
    grid-column-gap: 10px;


    margin-bottom: 1rem;
    overflow: auto;


    border-radius: 32px;
    background: linear-gradient(45deg, #d1ced4, #f8f5fb);
    box-shadow: 14px -14px 28px #a5a3a7,
        -14px 14px 28px #ffffff;

}

.profile-img {
    grid-area: profile-img;
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: space-evenly;
}

.profile-img>img {
    border-radius: 50%;
    margin: 2rem auto;
}

.profile-img a img {
    border-radius: 50%;
    margin: 2rem auto;
}

.profile-details {
    grid-area: profile-details;
    display: flex;
    flex-direction: column;
    height: auto;
    justify-content: center;
}

.profile-buttons {
    grid-area: profile-buttons;
    display: flex;
    flex-direction: column;
    height: auto;
    justify-content: center;
}

#profile-page .current-profile {
    height: 100vh;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(5, 1fr);
    grid-template-areas:
        "current-btn"
        "top-profile"
        "bio"
        "exp-edu"
        "review";
    grid-row-gap: 16px;
}

.current-btn {
    grid-area: current-btn;
}

.current-btn a {
    margin-right: 0.5rem;
    font-family: 'IBM Plex Sans', sans-serif;
}

.top-profile {
    display: flex;
    justify-content: space-evenly;
    align-items: center;


    padding: 2rem 2rem;


    background: #6C63FF;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;

}

.top-profile-img {
    grid-area: top-profile-img;
    width: 70vh;
}

.details {
    text-align: left;
}

.top-bio {
    grid-area: bio;
    background-color: #f4f4f4;

    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 2.5rem;


    background: #f4f4f4;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.exp-edu {
    grid-area: exp-edu;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(1, 1fr);
    grid-template-areas:
        "exp edu"
    ;
    grid-row-gap: 1rem;
    grid-column-gap: 1rem;
}

.exp-common-heading {
    color: #3f3d56;
    font-weight: bold;
    font-family: 'IBM Plex Sans', sans-serif;
    margin-bottom: 2rem;
    margin-top: 1.5rem;
}

.exp-common-details {
    font-family: 'IBM Plex Sans', sans-serif;
}

.exp-bottom {
    grid-area: exp;

    padding: 2rem;




    background: #f4f4f4;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.edu-bottom {
    grid-area: edu;

    padding: 2rem;



    background: #f4f4f4;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.patient-review {
    grid-area: review;
}

.text-area {
    display: block;
    width: 100%;
    padding: 0.4rem;
    margin: 1rem auto;
    font-size: 1.2rem;

   
    background: #f4f4f4;
