}
.scroll .card{
    background: #6C63FF;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    margin: 1.5rem;
}
.current-review {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(1, 1fr);
    grid-template-areas:
        "img-user user-review user-review user-review";
    grid-column-gap: 1rem;
    border: 2px solid #738f93;
    padding: 1rem;
    border-radius: 0.25rem;
    margin: 1rem auto;
}

.img-user {
    grid-area: img-user;
    padding: 1rem;
    text-align: center;
    font-family: 'IBM Plex Sans', sans-serif;
    color: #17a2b8;
    font-weight: bold;
}

.user-pic {
    margin-bottom: 1rem;
    width: 100px;
}

.user-review {
    grid-area: user-review;
    padding: 1rem;
    font-family: 'IBM Plex Sans', sans-serif;
}

.post-date {
    font-size: 0.9rem;
    color: white;
}

.modal-body .profiles .profile-details,
.modal-body .profiles .profile-buttons {
    text-align: center;
}

.modal-body .profiles .profile-1 {
    grid-template-columns: repeat(1, 1fr);
    grid-template-areas:
        "profile-img"
        "profile-details"
        "profile-buttons"
    ;
}

.carousel-item {
    text-align: center;
    padding: 5% 15%;
}

#testimonials {
    width: auto;
    text-align: center;
    margin-right: 3rem;
    padding: 2rem 3rem;
border-radius: 30px;
    box-shadow: rgb(0 0 0 / 35%) 0px 5px 15px;
    background:  linear-gradient(315deg, #d1ced4, #f8f5fb);

}

.scroll {
    height: 15rem;
    width: auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    overflow-y: scroll;
}

.scroll .card .card-img-top {
    margin-top: 1rem;
    align-self: center;
