}

#graph {
    width: auto;
    height: auto;
    text-align: center;
    /* background-color: #333; */
    border-radius: 0.25rem;
}

.user-graph {
    display: flex;
    flex-direction: column;
    height: 19.3rem;
    width: 34rem;
    justify-content: center;

    border-radius: 29px;


}

.appointment-doctor {
    display: flex;
    flex-wrap: wrap;
    margin: 0.2rem 0;
}

.appointment-img {
    width: 5rem;
    margin-right: 1rem;
}

.appointment-doctor p {
    align-self: center;
    font-weight: 500;
}

/* Tablets */
@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) and (-webkit-min-device-pixel-ratio: 1) {
    .appointment-p {
        text-align: center;
    }

    .welcome-heading {
        font-size: 1.5rem;
    }

    .common-table {
        width: auto;
    }

    #landing .container {
        grid-template-areas:
            "heading   heading"
            "img        img"
            "signup     signup";
    }

    #profiles-page .profiles {
        text-align: center;
        justify-content: normal;
    }

    .profile-1 {
        width: auto;
        grid-template-columns: repeat(1, 1fr);
        grid-template-areas:
            "profile-img"
            "profile-details"
            "profile-buttons"
        ;
        grid-column-gap: 0px;
        margin-bottom: 1rem;
    }

    .profile-2 {
        width: auto;
        grid-template-columns: repeat(1, 1fr);
        grid-template-areas:
            "profile-img"
            "profile-details"
            "profile-buttons"
        ;
        grid-column-gap: 0px;
        margin-bottom: 1rem;
    }

    .review-graph {
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(1, 1fr);
        grid-template-areas:
            "common-details""common-details"
            "common-details ";
        grid-row-gap: 1rem;
    }

    .profile-desc {
        text-align: center;
        padding: 2rem;
    }

    .desc-p {
        text-align: center;
    }

    .desc-p .profile-btn {
        align-self: center;
    }

    .heading-common h1 {
        font-size: 3rem;
    }

    .common-form {
        grid-template-columns: repeat(1, 1fr);
        grid-template-areas:
            "img-side"
            "img-side"
            "form-side"
            "form-side";
        grid-column-gap: 10px;
        grid-row-gap: 10px;
    }

    .register-user {
        width: 40vh;
    }

    .top-profile {
        flex-direction: column;
    }

    .top-profile {
        flex-direction: column;
    }

    .top-profile-img {
        display: none;
    }

}

/* Smart Phones */
@media(max-width: 575.98px) {
    .appointment-p {
        text-align: center;
    }

    .welcome-heading {
        font-size: 1.5rem;
    }

    #landing .container {
        grid-template-areas:
            "heading   heading"
            "img        img"
            "signup     signup";
    }

    .common-table {
        width: auto;
    }

    #profiles-page .profiles {
        text-align: center;
        justify-content: normal;
    }

    .profile-1 {
        width: auto;
        grid-template-columns: repeat(1, 1fr);
        grid-template-areas:
            "profile-img"
            "profile-details"
            "profile-buttons"
        ;
        grid-column-gap: 0px;
        margin-bottom: 1rem;
    }

    .profile-2 {
        width: auto;
        grid-template-columns: repeat(1, 1fr);
        grid-template-areas:
            "profile-img"
            "profile-details"
            "profile-buttons"
        ;
        grid-column-gap: 0px;
        margin-bottom: 1rem;
    }

    #profile-page .current-profile {
        grid-template-columns: repeat(1, 1fr);
    }

    .exp-edu {
        grid-template-columns: repeat(1, 1fr);
        grid-template-rows: repeat(1, 1fr);
        grid-template-areas:
            "exp"
            "edu";
    }

    .current-review {
        grid-template-columns: repeat(1, 1fr);
        grid-template-areas:
            "img-user "
            "user-review";
    }

    .review-graph {
        grid-template-columns: repeat(1, 1fr);
        grid-template-rows: repeat(1, 1fr);
        grid-template-areas:
            "common-details"
            "common-details";
        grid-row-gap: 1rem;
    }

    #profile-page .container {
        display: flex;
        justify-content: center;
    }

    .current-btn {
        display: flex;
        justify-content: space-around;
        height: 10rem;
        flex-direction: column;
    }

    .profile-desc {
        text-align: center;
        padding: 1rem;
    }

    .desc-p {
        text-align: center;
    }

    .desc-p .profile-btn {
        align-self: center;
    }

    .heading-common h1 {
        font-size: 3rem;
    }

    .common-form {
        grid-template-columns: repeat(1, 1fr);
        grid-template-areas:
            "img-side"
            "img-side"
            "form-side"
            "form-side";
        grid-column-gap: 10px;
        grid-row-gap: 10px;
    }

    .register-user {
        width: 40vh;
    }

    .top-profile {
        width: 366px;
        height: auto;
        flex-direction: column;
    }

    .details {
        text-align: center;
    }

    .top-img {
        text-align: center;
    }

    .top-profile-img {
        display: none;
    }

    .current-btn a {
        margin: 0 1rem;
    }
}

/* Smart Phones in Landscape */
@media(max-height: 500px) {
    .heading-common h1 {
        font-size: 3rem;
    }

    .appointment-p {
        text-align: center;
    }

    #landing .container {
        grid-template-areas:
            "heading   heading"
            "img        img"
            "signup     signup";
    }

    .common-table {
        width: auto;
        border-radius: 0px;

    }

    .profile-btn {
        font-size: 0.8rem;
    }

    .review-graph {
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(1, 1fr);
        grid-template-areas:
            "common-details""common-details"
            "common-details ";
        grid-row-gap: 1rem;
    }

    .profile-desc {
        text-align: center;
        padding: 2rem;
    }

    .desc-p {
        text-align: center;
    }

    .desc-p .profile-btn {
        align-self: center;
        background-color: #4038c9;
    }

    .common-form {
        grid-template-columns: repeat(1, 1fr);
        grid-template-rows: repeat(1, 1fr);
        grid-template-areas:
            "img-side"
            "form-side";
        grid-column-gap: 10px;
        grid-row-gap: 10px;
    }

    .img-side {
        display: flex;
        justify-content: flex-end;
    }

    .register-user {
        width: 100vh;
        z-index: -1;
    }

    .top-profile {
        flex-direction: column;
    }

    .top-profile-img {
        display: none;
    }
}

